let settingWritePhotosAlbum = false;
export const saveImg = function(url,callback) {
	if (url) {

		if (settingWritePhotosAlbum) {
			uni.getSetting({
				success: res => {
					if (res.authSetting['scope.writePhotosAlbum']) {
                        uni.showLoading({
                            title: ''
                        });
						uni.downloadFile({
							url: url,
							success: data => {
								if (data.statusCode == 200) {
									uni.saveImageToPhotosAlbum({
										filePath: data.tempFilePath,
										success: () => {
                                            uni.hideLoading();
											callback && callback();
											uni.showToast({
												title: ''
											});
										},
                                        fail(e) {
                                            uni.hideLoading();
                                            tip({
                                                title: '' + e.errMsg,
                                                icon: "none"
                                            });
                                        }
									});
								} else {
                                    uni.hideLoading();
                                    uni.showToast({
                                    	title: '',
                                        icon: "none"
                                    });
                                }
							},
                            fail(e) {
                                uni.hideLoading();
                                uni.showToast({
                                	title: '' + e.errMsg,
                                    icon: "none"
                                });
                            }
						});
					} else {
						uni.showModal({
							title: '',
							content: '',
							confirmText: '',
							cancelText: '',
							success: data => {
								if (data.confirm) {
									uni.openSetting();
								}
							}
						});
					}
				}
			});
		} else {
			settingWritePhotosAlbum = true;
			uni.authorize({
				scope: 'scope.writePhotosAlbum',
				success: () => {
                    uni.showLoading({
                        title: ''
                    });
					uni.downloadFile({
						url: url,
						success: data => {
							if (data.statusCode == 200) {
								uni.saveImageToPhotosAlbum({
									filePath: data.tempFilePath,
									success: () => {
                                        uni.hideLoading();
										callback && callback();
										uni.showToast({
											title: ''
										});
									},
                                    fail(e) {
                                        uni.hideLoading();
                                        tip({
                                            title: '' + e.errMsg,
                                            icon: "none"
                                        });
                                    }
								});
							} else {
                                uni.hideLoading();
                                uni.showToast({
                                    title: '',
                                    icon: "none"
                                });
                            }
                        },
                        fail(e) {
                            uni.hideLoading();
                            uni.showToast({
                                title: '' + e.errMsg,
                                icon: "none"
                            });
                        }
					});
				}
			});
		}


















































	} else {
		uni.showToast({
			title: '',
			icon: 'none'
		});
	}
};
